<template>
    <v-container class="pa-0">
    <!-- Header -->
    <PDHeader></PDHeader>

    <!-- Main -->
    <v-main class="pt-0 mt-5 px-5">
        <form @submit.prevent="submit">
            <v-text-field v-model="name.value.value" :counter="10" :error-messages="name.errorMessage.value"
                label=""></v-text-field>

            <v-text-field v-model="contactName.value.value" :counter="7" :error-messages="name.errorMessage.value"
                label=""></v-text-field>

            <v-text-field v-model="phone.value.value" :error-messages="phone.errorMessage.value"
                label=""></v-text-field>

            <v-select v-model="select.value.value" :items="question_type_list" :error-messages="select.errorMessage.value"
                label=""></v-select>

            <v-textarea label="" :error-messages="description.errorMessage.value"></v-textarea>

            <v-file-input accept="image/*" label="" prepend-icon="mdi-camera" chips variant="solo"  density="compact"></v-file-input>

            <v-btn class="me-4" type="submit">
                submit
            </v-btn>

            <v-btn @click="handleReset">
                clear
            </v-btn>
        </form>

    </v-main>

    <!-- Footer -->
    <PDFooter></PDFooter>
</v-container>
</template>

<script setup>
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue';
// import { ref } from 'vue'
// import { useStore } from 'vuex';
// 
import { useField, useForm } from 'vee-validate'

// Input
const { handleSubmit, handleReset } = useForm({
    validationSchema: {
        name(value) {
            if (value?.length >= 2) return true
            return ''
        },
        phone(value) {
            if (value?.length > 9 && /[0-9-]+/.test(value)) return true
            return ''
        },
        email(value) {
            if (/^[a-z.-]+@[a-z.-]+\.[a-z]+$/i.test(value)) return true
            return 'e-mail'
        },
        select(value) {
            if (value) return true
            return ''
        },
        description(value) {
            if (value) return true
            return ''
        },
    },

})

const name = useField('name');
const contactName=useField('contactName');
const phone = useField('phone');
const select = useField('select');
const description=useField('description');
const question_type_list=['','','',''];

const submit = handleSubmit((values) => {
    alert(JSON.stringify(values, null, 2))
})




</script>